$color-green: #0C0;
$color-amber: #fb7e14;

$color-primary: $color-amber;
$color-border: $color-primary;
.grid-container {
  width: 100%;
  display: flex;
  border: 1px solid gold;
  justify-content: space-around;
}
.grid {

  justify-self: center;
  overflow: hidden;
  max-width: 540px;
  max-height: 540px;
  display: grid;
  grid-template-rows: repeat(101, 40px);
  grid-gap: 10px 10px;
  border: 1px solid $color-border;
  padding: 10px;

  .row {
    // display:flex;
    // flex-direction: row;
    //border: 1px solid red;
    display: grid;
    grid-template-columns: repeat(101, 40px);
    height: 40px;
    grid-gap: 10px 10px;

    .cell {
      border: 1px solid #444;
      font-size: 13px;
      width: 40px;
      height: 40px;
      &.special {
        background: greenyellow;
      }
    }
  }
}